import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
  })
export class HomeComponent implements OnInit {

  start: boolean = false


  ngOnInit () {
    setTimeout(() => this.start = true, 200)
  }
 

}



